-legible';\n  border-bottom: 0.5px solid var(--binary-green);\n}\n.header-highlight {\n  color: var(--binary-green);\n}\nnav.header-nav > ul {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  gap:25px;\n  margin-top: 35px;\n}\nnav.header-nav > ul > li > button {\nwidth: 150px;\nheight: 40px;\nfont-size: 1.3rem;\ncolor: var(--electric-blue);\nbackground-color: var(--crt-gray);\nborder: 1.5px solid var(--binary-green);\nborder-radius: 5px;\nfont-weight: bold;\n}\nnav.header-nav > ul > li > button:hover {\n  color: var(--binary-green);\n  border-color: var(--electric-blue);\n  box-shadow: 0px 0px 5px var(--electric-blue);\n  font-weight: bolder;\n  }\n\n#current-page {\n  color: var(--binary-green);\n  border-color: var(--binary-green);\n  box-shadow: 0px 0px 6px var(--binary-green);\n  font-weight: bolder;\n}\n/*#endregion*/\n\n/* Index HERO */\n/*#region*/\nsection#hero-container {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding-top: 25px;\n  padding-bottom: 25px;\n}\n.hero-image {\n  width: 75vw;\n  max-height: 75vh;\n  position: relative;\n  display: flex;\n  border-radius: 10px;\n  box-shadow: var(--card-shadow);\n}\n.hero-overlay {\n  position: absolute;\n\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  padding: 50px;\n  background-color: #000000f2;\n  \n  color: var(--electric-blue);\n  border: 3px solid var(--binary-green);\n  border-radius: 10px;\n}\n.hero-overlay > h2 {\n  font-weight: 1000;\n}\n.hero-overlay > p {\n  margin-top: 5px;\n}\n.hero-overlay > button {\n  margin-top: 10px;\n  padding: 10px;\n  background-color: var(--crt-gray-compliment);\n  color: var(--electric-blue);\n  border: 2px solid var(--binary-green);\n  border-radius: 3px;\n\n}\n.hero-overlay > button:hover {\n  color: var(--binary-green);\n  border-color: var(--electric-blue);\n  box-shadow: 0px 0px 5px 1px var(--electric-blue);\n  font-weight: bold;\n}\n/*#endregion*/\n\n/* Reviews */\n/*#region*/\ndiv#reviews-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  gap: 75px;\n  padding-top: 50px;\n}\n\nsection#critic-review {\n  display: flex;\n  flex-direction: column;\n  padding: 10px;\n  gap: 5px;\n  background-color: var(--crt-gray-compliment);\n  color: var(--electric-blue);\n  border-radius: 25px;\n  box-shadow: var(--card-shadow);\n  max-width: 600px;\n  font-style: italic;\n}\nsection#critic-review div.critic-signature {\n  display: flex;\n  flex-direction: row-reverse;\n  color: var(--binary-green);\n  align-items: center;\n  justify-items: flex-end;\n  gap: 5px;\n  font-size: 1.2rem;\n}\n.critic-icon {\n  width: 35px;\n  align-self: center;\n  justify-self: center;\n  fill: black;\n  background-color: var(--electric-blue);\n  padding: 2.5px;\n  border-radius: 100%;\n}\n\nsection#total-and-customer-review {\n  display: flex;\n  flex-direction: row;\n  padding: 25px;\n  gap: 50px;\n}\n.total-reviews {\n  color: var(--electric-blue);\n}\n.total-reviews .star-icons-container {\n  display: flex;\n  justify-content: center;\n}\n.star-icon {\n  fill: var(--binary-green);\n  max-width: 75px;\n}\n.total-reviews-count {\n  font-size: 1.5rem;\n  font-weight: bolder;\n  text-align: center;\n  margin-bottom: -2px;\n}\n.total-reviews-text {\n  text-align: center;\n}\n.total-reviews-side-note {\n  font-style: italic;\n  font-size: .8rem;\n}\n.five-star-reviews {\n  font-size: 1.1rem;\n  color: var(--binary-green);\n}\n.customer-review-container {\n  display: flex;\n  flex-direction: row-reverse;\n  justify-content: flex-end;\n  align-items: center;\n  flex-wrap: wrap;\n  max-width: 500px;\n  background-color: var(--crt-gray-compliment);\n  padding: 15px;\n  border-radius: 10px;\n  box-shadow: var(--card-shadow);\n}\n.customer-icon {\n  display: flex;\n  align-items: center;\n  position: relative;\n}\n.incognito-icon-swap {\n  position: absolute;\n  width: 50px;\n  fill: black;\n  background-color: var(--electric-blue);\n  border-radius: 100%;\n  padding: 2px;\n}\n.incognito-icon-swap:hover {\n  opacity: 0;\n}\n.alien-icon {\n  width: 50px;\n  fill: var(--binary-green);\n  background-color: purple;\n  border-radius: 100%;\n  padding: 2px;\n}\n.customer-name {\n  color: var(--binary-green);\n  font-weight: bold;\n  font-size: 1.2rem;\n  padding-left: 17px;\n}\n.customer-review {\n  color: var(--electric-blue);\n  font-style: italic;\n  padding-top: 2.5px;\n}\n.featured-in {\n  grid-column: 1 / -1;\n  grid-row: 2;\n  justify-self: center;\n  align-self: center;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n.featured-in-title {\n  color: var(--binary-green);\n  font-size: 2rem;\n  font-weight: bolder;\n  margin-bottom: -25px;\n  text-align: center;\n}\n.featured-in-logo-container {\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  align-items: center;\n}\n/*#endregion*/\n\n/* Newsletter */\n/*#region*/\nsection#newsletter-container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 15px;\n  padding: 50px;\n  background-color: var(--crt-gray-compliment);\n}\nform#newsletter-signup-form {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 12px;\n}\nsection#newsletter-container > h2 {\n  color: var(--binary-green);\n  font-weight: bold;\n  font-size: 2rem;\n}\nsection#newsletter-container > h4 {\n  color: var(--electric-blue);\n  font-style: italic;\n}\ninput#newsletter-email {\n  min-width: 300px;\n  min-height: 30px;\n  border: 2px solid var(--electric-blue);\n  padding: 10px;\n  font-weight: bolder;\n}\ninput#newsletter-email:focus {\n  box-shadow: var(--card-shadow);\n}\ndiv.newsletter-options-container {\n  color: var(--electric-blue);\n  display: flex;\n}\ninput#newsletter-option-tech:checked + label[for=\"newsletter-option-tech\"] {\n  color:var(--binary-green);\n}\nlabel[for=\"newsletter-option-tech\"] {\n  padding-right: 25px;\n  padding-left: 5px;\n}\ninput#newsletter-option-restaurant:checked + label[for=\"newsletter-option-restaurant\"] {\n  color:var(--binary-green);\n}\nlabel[for=\"newsletter-option-restaurant\"] {\n  padding-left: 5px;\n}\nbutton#newsletter-submit {\n  width: 200px;\n  height: 50px;\n  font-size: 1.3rem;\n  color: var(--electric-blue);\n  background-color: var(--crt-gray);\n  border: 1.5px solid var(--binary-green);\n  border-radius: 5px;\n  font-weight: bold;\n}\nbutton#newsletter-submit:hover {\n  color: var(--binary-green);\n  border-color: var(--electric-blue);\n  box-shadow: 0px 0px 5px 1px var(--electric-blue);\n  font-weight: bolder;\n}\n/*#endregion*/\n\n\n/* Details */\n/*#region*/\n/*#endregion*/\n\n/* Footer */\n/*#region*/\nfooter#footer-container {\n  border-bottom: 1.5px solid var(--electric-blue);\n  border-top: 1.5px solid var(--electric-blue);\n  display: flex;\n  gap: 15px;\n  justify-content: center;\n  align-items: center;\n  background-color: var(--crt-gray-compliment);\n  color: var(--electric-blue);\n  font-weight: bold;\n  font-size: 1.5em;\n  padding: 20px;\n}\n.github-logo-link {\n  width: 60px;\n  fill: var(--binary-green);\n}\n.github-logo-link:hover {\n  fill: var(--electric-blue);\n  border-radius: 100%;\n  box-shadow: var(--card-shadow);\n}\n/*#endregion*/"],"sourceRoot":""}]);
